import React, { useEffect, useRef } from 'react';
import moment from 'moment-timezone';

import TagLabel from 'components/label/Tag';

import './index.scss';
import { Link } from 'gatsby';
import { parseTitleForPath } from 'utils/text-parser';

type Props = {
  image?: string;
  title: string;
  tags: { id: string; name: string }[];
  writer: string;
  writeAt: string;
};

function PreviewCard({ image, title, tags, writer, writeAt }: Props) {
  const titleContainerRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!image && titleContainerRef.current) {
      const gradientStyle = `linear-gradient(45deg, #fff, #fff)`;
      titleContainerRef.current.style.background = gradientStyle;
    }
  }, [image, titleContainerRef]);

  return (
    <Link className='preview-card' to={`/post/${parseTitleForPath(title)}`}>
      {image ? (
        <img className='preview-card__image' src={image} alt='preview' />
      ) : (
        <div className='preview-card__image' ref={titleContainerRef}>
          <div className='preview-card__image__title'>{title}</div>
        </div>
      )}

      <div className='preview-card__body'>
        <div className='preview-card__title'>{title || '제목없음'}</div>
        {writer || writeAt ? (
          <p className='preview-card__description'>
            {moment(writeAt).tz('Asia/Seoul').format('YYYY.MM.DD')}
            {writer && ` ・ ${writer}`}
          </p>
        ) : (
          <></>
        )}
        <div className='preview-card__tags'>
          {tags?.map((tag, index) => (
            <TagLabel tag={tag} key={`${tag}-${index}`} />
          ))}
        </div>
      </div>
    </Link>
  );
}

export default PreviewCard;
